<template>
	<view class="login">
		<!-- <navigation-bar :title="nbTitle" :title-icon="titleIcon" :subtitle-text="subtitleText"
			:subtitle-color="nbFrontColor" :loading="nbLoading" :front-color="nbFrontColor"
			:background-color="nbBackgroundColor" :background-image="img" :color-animation-duration="2000"
			color-animation-timing-func="easeIn">
		</navigation-bar> -->
		<uni-nav-bar left-icon="left" right-icon="cart" title="标题" />
		<view class="uni-form-item">
			用户名：<input class="uni-input" @input="onKeyUser" focus placeholder="请输入用户名" />
			密码：<input class="uni-input" @input="onKeyPsd" password type="text" placeholder="请输入密码" />
		</view>
		<view>
			<button class="onlogin" @click.native="onLogin">点击登录</button>
			<navigator url="/components/register/register">
				<button class="onregister">注册</button>
			</navigator>
		</view>
	</view>
</template>

<script>
	import {
		getLogin
	} from '@/api/login.js'
	export default {
		data() {
			return {
				// 自动获取焦点
				focus: true,
				password: true,
				// 用户名
				inputUser: '',
				// 密码
				inputPsd: '',
				/* nbTitle: '标题',
				subtitleText: 'subtitleText',
				nbLoading: false,
				nbFrontColor: '#000000',
				nbBackgroundColor: '#ffffff',
				img: '/static/logintop.jpg' */
			
			}
		},
		// onLoad() {
		// 	this.onLogin()
		// },
		methods: {
			onKeyUser: function(event) {
				this.inputUser = event.target.value
			},
			onKeyPsd: function(event) {
				this.inputPsd = event.target.value
			},
			onLogin() {
				let token = {
					username: this.inputUser,
					password: this.inputPsd
				}
				getLogin(token).then(data => {
					console.log(data);
				})
			}
		}
	}
</script>

<style scoped lang="scss">
	.login {
		width: 100vw;
		background-color: aliceblue;
		margin: 0px auto;
		.uni-form-item {
			width: 90vw;
			margin: 0 auto;
			background-color: white;
			.uni-input {
				width: 100vw;
				margin-top: 20px;
				border-bottom: 1px solid #333;
				
			}
		}.onlogin {
					padding: 12px;
					width: 50vw;
					background-color: #0bb861;
					color: white;
				}
				.onregister {
					margin-top: 20px;
					padding: 12px;
					width: 50vw;
					background-color: #3697fd;
					color: white;
				}
	}
</style>
